// App.js

import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { HashRouter as Router, Switch, Route, Link } from 'react-router-dom';

// Routes
import Home from './Home';
import About from './About';
import SynXis from './pages/SynXis';
import Project2 from './pages/Project2';
import Logo from './assets/images/dd-logo.svg'





class App extends Component {
  render() {
    return (
      
    <Router>

      
        <div className="content">

    
          <div class="row navigation">
          <div class="col-6">
            <div className="nav-left">
            <Link to="/" className="item"><div class="header-logo"><img className="header-logo" src={Logo} /></div></Link>
            </div>
          </div>
          <div class="col-6">
          <div className="nav-right">
            <Link to="/about" className="item"><span className="nav-text about">About</span></Link>
            <Link to="/resume" className="item"><span className="nav-text">Resume</span></Link>
          </div>
          </div>
        </div>

        
        {/* Set up the Router */}
        <Route exact path="/" component={Home} />
        <Route path="/about" component={About} />
        <Route path="/resume" component={About} />
        <Route path="/SynXis" component={SynXis} />
        <Route path="/Project2" component={Project2} />

      </div>
      </Router>
    );
  }
}

export default App;